

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
    margin: 0;
    padding: 0;
    border: 0;
    font-size: 100%;
    font: inherit;
    vertical-align: baseline;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
    display: block;
}

html {
    height: 100%;
}

body {
    line-height: 1;
}

ol,
ul {
    list-style: none;
}

blockquote,
q {
    quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
    content: '';
    content: none;
}

.left_part_of_page {
    width: 50%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
}

.content_box {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
    padding-left: 70px;
    padding-top: 80px;
}

.content_title {
    font-size: 30px;
    font-weight: 700;
    line-height: 35px;
    letter-spacing: 0em;
    text-align: left;
    color: #3751FE;
    padding-left: 6%;
}

.content_subtitle {
    font-size: 36px;
    font-weight: 700;
    line-height: 49px;
    letter-spacing: 0em;
    text-align: left;
    color: #3751FE;
    padding-top: 80px;
}

.content_subtext {
    color: #00000099;
    font-size: 18px;
    font-weight: 600;
    padding-top: 20px;
    line-height: 21px;
    letter-spacing: 0em;
    text-align: left;
}


.content_login_row_btns {
    display: flex;
    justify-content: space-between;
}

.content_login_row_button_row_subtext {
    font-size: 18px;
    font-weight: 400;
    line-height: 21px;
    letter-spacing: 0em;
    text-align: left;
}

.content_login_row_btn {
        font-size: 18px;
        font-weight: 600;
        color: #3751FE;
        line-height: 21px;
        letter-spacing: 0em;
        text-align: center;
        border-radius: none;
        background: none;
        border: none;
        cursor: pointer;
}